<template>
    <div>
        <input type="text" v-model="name">
        <input type="text" v-model="address">
        <input type="text" v-model="faculty">
        <input type="text" v-model="accred">
        <input type="text" v-model="rank">
        <button @click="adduniver">Add</button>
    </div>
</template>

<script >

export default{
    name:"AddUniver",
    data(){
        return{
            name:null,
            address:null,
            faculty:null,
            accred:null,
            rank:null
        }
    },
    methods:{
        adduniver(){
            this.$store.dispatch('addUniver',this.name,this.address,this.faculty,this.accred,this.rank)
        }
    }
}
</script>

