<template>
  <div class="hello">
    <input type="text" v-model="searchString">
    <div>
      <div v-for="univer in getUniverByrequest" :key="univer.id">
        <div>
            <p>{{ univer.name }}</p>
            <p>{{ univer.address }}</p>
            <p>{{ univer.faculties }}</p>
            <p>{{ univer.accreditationLevel }}</p>
            <p>{{ univer.ranking }}</p>            
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
export default {
  name: "GetUniver",
  data(){
    return{
      searchString: null
    }
  },
  computed:{
    ...mapGetters(['getAllUnivers']),
    getUniverByrequest(){
      return this.getAllUnivers(this.searchString)
    }
  },
};
</script>


