<template>
    <div>
      <input type="text" v-model="delString" placeholder="Enter name to delete">
      <button @click="deleteUniver">Delete</button>
    </div>
  </template>
  
  <script>
  export default {
    name: "DeleteUniver",
    data() {
      return {
        delString: null,
      };
    },
    methods: { 
      deleteUniver() {
        return this.$store.dispatch('deleteFromRequest', this.delString);
      },
    },
  };
  </script>
  
  <style lang="scss" scoped>
  </style>
  